// —Å—Ç—Ä–æ–∫–æ–≤—ã–µ –∫–æ–Ω—Å—Ç–∞–Ω—Ç—ã
const CHECKED_CLASS_NAME = "checked";
const CHANGED_CLASS_NAME = "changed";
const HIDDEN_CLASS_NAME = "hidden";
const CHECKED_ACTION = "checked";
const DELETE_ACTION = "delete";
const EDIT_ACTION = "edit";
const SAVE_ACTION = "save";

// –∫–æ–Ω—Å—Ç–∞–Ω—Ç—ã –∏–∑ html
const formNode = document.getElementById("add__app-form");
const inputNode = document.getElementById("add__app-input");
const addBtnNode = document.getElementById("btn__add-film");
const listNode = document.getElementById("add__app-list");

let films = [];

const filmsFromStorage = localStorage.getItem("films");

if (filmsFromStorage) {
  films = JSON.parse(filmsFromStorage);
  films.forEach((film) => renderFilm(film));
}

countFilmsElements();

// —Ñ—É–Ω–∫—Ü–∏–∏----------------------------------------------

// –ø–æ–ª—É—á–µ–Ω–∏–µ —Ä–æ–¥–∏—Ç–µ–ª—è —Ç–∞—Ä–≥–µ—Ç–∞
const getParentNode = (e) => e.target.closest(".add__app-film");

// –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫–∏ —Å data-action
function addFilmListeners(e) {
  const targetEl = e.target.dataset.action;
  switch (true) {
    // —É–¥–∞–ª–µ–Ω–∏–µ —Ñ–∏–ª—å–º–∞
    // –µ—Å–ª–∏ click –ø–æ –¥–∞—Ç–µ "delete", —Ç–æ –≤—ã–ø–æ–ª–Ω—è–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    case targetEl === DELETE_ACTION:
      deleteFilm(e);
      break;
    // —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ
    // –µ—Å–ª–∏ click –ø–æ –¥–∞—Ç–µ "edit", —Ç–æ –≤—ã–ø–æ–ª–Ω—è–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    case targetEl === EDIT_ACTION:
      editFilm(e);
      break;
    // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ
    // –µ—Å–ª–∏ click –ø–æ –¥–∞—Ç–µ "save", —Ç–æ –≤—ã–ø–æ–ª–Ω—è–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    case targetEl === SAVE_ACTION:
      saveEdit(e);
      break;
    // –æ—Ç–º–µ—á–∞–µ–º —Ñ–∏–ª—å–º –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã–º
    // –µ—Å–ª–∏ click –ø–æ –¥–∞—Ç–µ "checked", —Ç–æ –≤—ã–ø–æ–ª–Ω—è–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    case targetEl === CHECKED_ACTION:
      checkedFilm(e);
      break;
    default:
      return;
  }
}

// —É–¥–∞–ª–µ–Ω–∏–µ —Å–ª—É—à–∞—Ç–µ–ª–µ–π —Å –≠–õ–ï–ú–ï–ù–¢–ê, –≤ –Ω–∞—à–µ–º —Å–ª—É—á–∞–µ —Å li(–¥–∞–ª–µ–µ parentNode)
function removeFilmListeners(el) {
  el.removeEventListener("click", addFilmListeners);
}

// —Ä–µ–Ω–¥–µ—Ä –∫–∞–∂–¥–æ–≥–æ –Ω–æ–≤–æ–≥–æ —Ñ–∏–ª—å–º–∞
function renderFilm(film) {
  // —Ñ–æ—Ä–º–∏—Ä—É–µ–º css class –¥–ª—è —Å–æ—Å—Ç–æ—è–Ω–∏—è checked
  const cssClassChecked = film.checked
    ? "add__app-film checked"
    : "add__app-film";
  const cssClassChanged = film.changed ? "hidden changed" : "hidden";
  // —Ñ–æ—Ä–º–∏—Ä—É–µ–º —Ä–∞–∑–º–µ—Ç–∫—É –¥–ª—è –Ω–æ–≤–æ–≥–æ —Ñ–∏–ª—å–º–∞
  const filmHTML = `
  <li id="${film.id}" class="${cssClassChecked}">
    <div class="col__left">
      <button data-action="checked" class="btn btn__check-film">
        <img src="resources/unchecked.png" alt="no" />
      </button>
      <form class="edit__film-form">
        <input class="film__title" readonly value="${film.text}" />
        <p class="${cssClassChanged}">–∏–∑–º–µ–Ω–µ–Ω–æ</p>
        <button data-action="save" class="btn btn__save-edit hidden">
          <img src="resources/favicon.ico" class="save__img" alt="" />
        </button>
      </form>
    </div>
    <div class="col__right">
      <button data-action="edit" class="btn btn__edit-film">
        <img src="resources/edit.png" class="edit__img" alt="" />
      </button>
      <button data-action="delete" class="btn btn__delete-film">
        <img src="resources/btn-cross.png" alt="" />
      </button>
    </div>
  </li>`;

  // –¥–æ–±–∞–≤–ª—è–µ–º –µ–µ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
  listNode.insertAdjacentHTML("beforeend", filmHTML);
}

// –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —Ñ–∏–ª—å–º–∞
function addFilm(e) {
  // –æ—Ç–º–µ–Ω—è–µ–º –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã
  e.preventDefault();

  // –¥–æ—Å—Ç–∞–µ–º —Ç–µ–∫—Å—Ç –∏–∑ –∏–Ω–ø—É—Ç–∞
  // —É–¥–∞–ª—è—è –ø—Ä–∏ —ç—Ç–æ–º –ø—Ä–æ–±–µ–ª—ã –≤ –∫–æ–Ω—Ü–µ –∏ –Ω–∞—á–∞–ª–µ —Å—Ç—Ä–æ–∫–∏
  const filmText = inputNode.value.trim();

  // –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ–±—ã —Ç–µ–∫—Å—Ç –±—ã–ª –≤–≤–µ–¥–µ–Ω
  if (!filmText) {
    alert("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ñ–∏–ª—å–º–∞");
    return;
  }

  // —Å–æ–∑–¥–∞—ë–º –æ–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –∏–∑ –Ω–æ–≤–æ–≥–æ —Ñ–∏–ª—å–º–∞
  const newFilm = {
    id: Date.now(),
    initialText: filmText,
    text: filmText,
    checked: false,
    changed: false,
  };

  // –¥–æ–±–∞–≤–ª—è–µ–º —ç—Ç–æ—Ç –æ–±—ä–µ–∫—Ç(—Ñ–∏–ª—å–º) –≤ –º–∞—Å—Å–∏–≤
  films.push(newFilm);
  console.log(films);

  // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
  saveFilmsToStorage();

  renderFilm(newFilm);

  // –æ—á–∏—â–∞–µ–º –∏–Ω–ø—É—Ç –∏ –æ—Å—Ç–∞–≤–ª—è–µ–º —Ñ–æ–∫—É—Å –Ω–∞ –Ω–µ–º
  inputNode.value = "";
  inputNode.focus();

  countFilmsElements();
}

// —É–¥–∞–ª–µ–Ω–∏–µ —Ñ–∏–ª—å–º–∞
function deleteFilm(e) {
  const parentNode = getParentNode(e);

  // —É–¥–∞–ª–µ–Ω–∏–µ –Ω–∞ —É—Ä–æ–≤–Ω–µ –¥–∞–Ω–Ω—ã—Ö
  // –æ–ø—Ä–µ–¥–µ–ª—è–µ–º id —Ç–µ–≥–∞ li(—ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞ films)
  const id = parseInt(parentNode.id);

  // —É–¥–∞–ª–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–∞ –∏–∑ –º–∞—Å—Å–∏–≤–∞ —Å –ø–æ–º–æ—â—å—é —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏
  films = films.filter((film) => film.id !== id);

  // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
  saveFilmsToStorage();

  // —É–¥–∞–ª–µ–Ω–∏–µ –Ω–∞ —É—Ä–æ–≤–Ω–µ —Ä–∞–∑–º–µ—Ç–∫–∏
  // —É–¥–∞–ª—è–µ–º —Ä–æ–¥–∏—Ç–µ–ª—è —Ç–∞—Ä–≥–µ—Ç–∞
  parentNode.remove();
  console.log(films);

  removeFilmListeners(parentNode);

  countFilmsElements();
}

// –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤ —ç–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫–∞ –Ω–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã–π(checked)
function checkedFilm(e) {
  // —Ç–æ–∂–µ —Å–∞–º–æ–µ —Ç—É—Ç
  // —Ç–æ–ª—å–∫–æ –º–µ–Ω—è–µ–º –∫–ª–∞—Å—Å —É —ç–ª–µ–º–µ–Ω—Ç–∞
  const parentNode = getParentNode(e);
  console.log(parentNode);

  // –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤(checked) –Ω–∞ —É—Ä–æ–≤–Ω–µ –¥–∞–Ω–Ω—ã—Ö
  const id = parseInt(parentNode.id);
  const film = films.find((film) => film.id === id);
  film.checked = !film.checked;

  // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
  saveFilmsToStorage();

  // —Ç–æ–≥–ª–∏–º –∫–ª–∞—Å—Å –Ω–∞ —É—Ä–æ–≤–Ω–µ —Ä–∞–∑–º–µ—Ç–∫–∏
  parentNode.classList.toggle(CHECKED_CLASS_NAME);
}

// —Ä–µ–¥–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —Ñ–∏–ª—å–º–∞
function editFilm(e) {
  const parentNode = getParentNode(e);

  // –∞ —Ç—É—Ç –º—ã –æ–±—Ä–∞—â–∞–µ–º—Å—è –∫ —ç–ª–µ–º–µ–Ω—Ç—É –≤–Ω—É—Ç—Ä–∏ —Ä–æ–¥–∏—Ç–µ–ª—è
  const editBtnNode = parentNode.querySelector(".btn__edit-film");
  const saveBtnNode = parentNode.querySelector(".btn__save-edit");
  const filmTitle = parentNode.querySelector(".film__title");

  saveBtnNode.classList.remove(HIDDEN_CLASS_NAME);
  editBtnNode.classList.add(HIDDEN_CLASS_NAME);
  filmTitle.removeAttribute("readonly");
  filmTitle.focus();
}

// —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏–π
function saveEdit(e) {
  // –∞–Ω–∞–ª–æ–≥–∏—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –≤–µ—Ä—Ö–Ω–µ–π
  e.preventDefault();
  const parentNode = getParentNode(e);

  const editBtnNode = parentNode.querySelector(".btn__edit-film");
  const saveBtnNode = parentNode.querySelector(".btn__save-edit");
  const textHiddenNode = parentNode.querySelector(".hidden");
  const filmTitle = parentNode.querySelector(".film__title");
  saveBtnNode.classList.add(HIDDEN_CLASS_NAME);
  editBtnNode.classList.remove(HIDDEN_CLASS_NAME);
  filmTitle.setAttribute("readonly", true);

  //–∏–∑–º–µ–Ω—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ —Ñ–∏–ª—å–º–∞ –Ω–∞ changed –Ω–∞ —É—Ä–æ–≤–Ω–µ –¥–∞–Ω–Ω—ã—Ö
  const id = parseInt(parentNode.id);
  const film = films.find((film) => film.id === id);

  if (!filmTitle.value.trim()) {
    alert("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ñ–∏–ª—å–º–∞");
    film.changed = false;
    textHiddenNode.classList.remove(CHANGED_CLASS_NAME);
    return (filmTitle.value = film.initialText);
  }
  // –µ—Å–ª–∏ –∏–∑–Ω–∞—á–∞–ª—å–Ω—ã–π —Ç–µ–∫—Å—Ç –Ω–µ —Ä–∞–≤–µ–Ω –Ω–æ–≤–æ–º—É
  if (film.initialText !== filmTitle.value) {
    // –¥–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å –Ω–∞ —É—Ä–æ–≤–Ω–µ —Ä–∞–∑–º–µ—Ç–∫–∏
    textHiddenNode.classList.add(CHANGED_CLASS_NAME);
    // –º–µ–Ω—è–µ–º —Å—Ç–∞—Ç—É—Å –Ω–∞ —É—Ä–æ–≤–Ω–µ –¥–∞–Ω–Ω—ã—Ö
    film.changed = true;
    film.text = filmTitle.value.trim();
  } else {
    // —É–±–∏—Ä–∞–µ–º –∫–ª–∞—Å—Å –Ω–∞ —É—Ä–æ–≤–Ω–µ —Ä–∞–∑–º–µ—Ç–∫–∏
    textHiddenNode.classList.remove(CHANGED_CLASS_NAME);
    // –º–µ–Ω—è–µ–º —Å—Ç–∞—Ç—É—Å –Ω–∞ —É—Ä–æ–≤–Ω–µ –¥–∞–Ω–Ω—ã—Ö
    film.changed = false;
    film.text = film.initialText;
  }

  // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
  saveFilmsToStorage();
}

// –ø—Ä–æ–≤–µ—Ä–∫–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–æ–≤ –≤ –º–∞—Å—Å–∏–≤–µ
function countFilmsElements() {
  if (films.length === 0) {
    const emptyListHTML = `
    <li id="empty__item-list" class="empty__item-list">
      <img src="resources/empty.png" alt="" />
    </li>`;
    listNode.insertAdjacentHTML("afterbegin", emptyListHTML);
  }

  if (films.length > 0) {
    const emptyListElement = document.getElementById("empty__item-list");
    emptyListElement ? emptyListElement.remove() : null;
  }
}

// —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –º–∞—Å—Å–∏–≤–∞ films –≤ LocalStorage
function saveFilmsToStorage() {
  localStorage.setItem("films", JSON.stringify(films));
}

// –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π----------------------------------

// –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç —Å–ø–∏—Å–∫–∞/—Ñ–∏–ª—å–º (li)
listNode.addEventListener("click", addFilmListeners);

// –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —Ñ–∏–ª—å–º–∞
formNode.addEventListener("submit", addFilm);
